import { Component, OnInit } from '@angular/core';

import { NgForm } from '@angular/forms';
import { UserService } from '../../../shared/user.service'
import { environment } from '../../../../environments/environment';
import {MatDialogRef} from '@angular/material/dialog';

@Component({
  selector: 'app-signup',
  templateUrl: './signup.component.html',
  styleUrls: ['./signup.component.css']
})
export class SignupComponent implements OnInit {
  emailRegex = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
  showSucessMessage: boolean = false
  serverErrorMessages: string;
  confirmPassword: string;


  constructor(public userService: UserService,public dialogRef: MatDialogRef<SignupComponent>) { }

 

  ngOnInit(): void {
    this.resetSelectedUser();
 
  }

  googlel() {

    var w = (screen.width - 800)/2
    var h = (screen.height - 600)/4
    var win = window.open(environment.apiBaseUrl + '/googlelll', "mywindow", "location=1, resizable=yes, status=1,scrollbars=1, width=800,height=600  top="+h+", left="+w);

    var timer = setInterval(() => {
      if (win.closed) {

        console.log("closed");
        this.dialogRef.close();
        clearInterval(timer);

      }
    }
      , 500);

  }
 
  onNoClick(): void {
    this.dialogRef.close();
  }
  
  onSubmit(form: NgForm) {

    this.userService.postUser(form.value).subscribe(  
      res => {
        this.showSucessMessage = true;
        setTimeout(() => this.showSucessMessage = false, 3000);
        this.resetForm(form);
        setTimeout(() => this.dialogRef.close() , 3000);
        
    
      },
      err => {
        if (err.status === 422) {
          this.serverErrorMessages = err.error.join('<br/>');
        }
        else
          this.serverErrorMessages = 'Something went wrong.';
      }
    );
  
}


resetSelectedUser(){
  this.userService.selectedUser = {
    fullName: '',
    email: '',
    password: ''
  };
 }


  resetForm(form: NgForm) {
    this.userService.selectedUser = {
      fullName: '',
      email: '',
      password: ''
    };
    form.resetForm();
    this.serverErrorMessages = '';
  }



}
