import { Component, OnInit } from '@angular/core';
import { Router } from "@angular/router";
import { environment } from '../../../../environments/environment';
import { NgForm } from "@angular/forms";
import {MatDialogRef} from '@angular/material/dialog';

import { UserService } from '../../../shared/user.service';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {

  constructor(private userService: UserService,private router : Router,public dialogRef: MatDialogRef<LoginComponent>) { }

  
  model ={
    email :'',
    password:''
  };
  emailRegex = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
  serverErrorMessages: string;

  ngOnInit(): void {
  }


  googlel() {

    var w = (screen.width - 800)/2
    var h = (screen.height - 600)/4
    var win = window.open(environment.apiBaseUrl + '/googlelll', "mywindow", "location=1, resizable=yes, status=1,scrollbars=1, width=800,height=600  top="+h+", left="+w);

    var timer = setInterval(() => {
      if (win.closed) {

        console.log("closed");
        this.dialogRef.close();
        clearInterval(timer);

      }
    }
      , 500);

  }



  onNoClick(): void {
    this.dialogRef.close();
  }

  onSubmit(form : NgForm){
    this.userService.login(form.value).subscribe(
      res => {

// /*next line is my new line*/ 
// this.userService.userMode(true);

        this.userService.setToken(res['token']);
        // this.router.navigateByUrl('/home');
        this.userService.islog = true  
        this.dialogRef.close();
         

      },
      err => {
        this.serverErrorMessages = err.error.message;
      }
    );
  }

}
