import { Component, OnInit } from '@angular/core';
import { PostService } from '../../shared/post.service';

import Quill from 'quill'

import ImageResize from 'quill-image-resize-module'
Quill.register('modules/imageResize', ImageResize)

import ImageCompress from 'quill-image-compress';
Quill.register('modules/imageCompress', ImageCompress);

@Component({
  selector: 'app-post',
  templateUrl: './post.component.html',
  styleUrls: ['./post.component.css']
})
export class PostComponent implements OnInit {

  postb



  model ={
    topic :'',
    postbody:''
  };

  modules = {}

  constructor(private postService: PostService) {

    this.modules = {
   
      imageResize: {},
      imageCompress: {
        quality: 0.8, // default is 0.7
        maxWidth: 1000, // default
        maxHeight: 1000, // default
        imageType: 'image/png', 
        debug: true, // default
      },
      toolbar: [
        ['bold', 'italic', 'underline', 'strike'],        // toggled buttons
        ['blockquote', 'code-block'],
     
        [{ 'header': 1 }, { 'header': 2 }],               // custom button values
        [{ 'list': 'ordered'}, { 'list': 'bullet' }],
        [{ 'script': 'sub'}, { 'script': 'super' }],      // superscript/subscript
        [{ 'indent': '-1'}, { 'indent': '+1' }],          // outdent/indent
        [{ 'direction': 'rtl' }],                         // text direction
     
        [{ 'size': ['small', false, 'large', 'huge'] }],  // custom dropdown
        [{ 'header': [1, 2, 3, 4, 5, 6, false] }],
     
        [{ 'color': [] }, { 'background': [] }],          // dropdown with defaults from theme
        [{ 'font': [] }],
        [{ 'align': [] }],
     
        ['clean'],                                         // remove formatting button
     
        ['link', 'image', 'video']             
      ]
      
    }

   }




loading:boolean=false
ok:boolean=false

  ngOnInit(): void {
  }


  createPost(){
 
 if(confirm("As this site is on development stage you cannot edit your post after you save it , Are you sure?")){
    this.loading=true
this.postService.addPost(this.model).subscribe(
  res=>{
    this.loading=false
    this.model.postbody=''
    this.model.topic=''
    this.ok=true
    setTimeout( ()=> this.ok=false , 1500);

    
  },
  err=>{
    this.loading=false
    alert("Somthing went Wrong")
    console.log("err"+  JSON.stringify( err))
  }
)}
}


}
