import { Injectable } from '@angular/core';

import { HttpClient, HttpHeaders } from '@angular/common/http';
import { environment } from '../../environments/environment';

@Injectable({
  providedIn: 'root'
})
export class PostService {

  constructor(private http: HttpClient) { }


  getPosts(page) {
    return this.http.get(environment.apiBaseUrl + `/posts/${page}`);
  }

  getAPost(id) {
    return this.http.get(environment.apiBaseUrl + `/post/${id}`);
  }


  deletePost(id) {
    return this.http.delete(environment.apiBaseUrl + `/post/delete/${id}`);
  }

  addPost(post) {
    return this.http.post(environment.apiBaseUrl + '/addpost', post);
  }


}
