import { Injectable } from '@angular/core';

import { HttpClient, HttpHeaders } from '@angular/common/http';

import { environment } from '../../environments/environment';
import { User } from './user.model';


@Injectable({
  providedIn: 'root'
})
export class UserService {
  constructor(private http: HttpClient) { }

  selectedUser: User = {
    fullName: '',
    email: '',
    password: ''
  };


  islog:boolean=false;


  getUsers(){
    return this.http.get(environment.apiBaseUrl + '/userlist');
  }

  getanuser(id){
    return this.http.get(environment.apiBaseUrl + `/user/${id}`);
  }
  

 deleteuser(id){
    return this.http.delete(environment.apiBaseUrl + `/user/delete/${id}`);
  }

    postUser(user: User){
    return this.http.post(environment.apiBaseUrl+'/register',user);
    }

    login(authCredentials) {
      return this.http.post(environment.apiBaseUrl + '/signin', authCredentials);
    }

    getUserProfile() {
      return this.http.get(environment.apiBaseUrl + '/userProfile');
    }   


    googlelogin(){
      return this.http.get(environment.apiBaseUrl + '/googlelll');
    }


    setToken(token: string) {
      localStorage.setItem('token', token);
    }
  
    getToken() {
      return localStorage.getItem('token');
    }
  
    deleteToken() {
      localStorage.removeItem('token');
    }
  
   

    getUserPayload() {
      var token = this.getToken();
      if (token) {
        var userPayload = atob(token.split('.')[1]);
        return JSON.parse(userPayload);
      }
      else
        return null;
    }
  
    isLoggedIn() {
      var userPayload = this.getUserPayload();
      if (userPayload)
        return userPayload.exp > Date.now() / 1000;
      else
        return false;
    }

getuserforothers(id){

  return this.http.get(environment.apiBaseUrl + `/getuserforothers/${id}`);

}



}
