import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

// import { AuthGuard } from './auth/auth.guard';
import { PagenumberGuard } from './aGuard/pagenumber.guard';

import { CAdminComponent } from './c-admin/c-admin.component';
import { CUserComponent } from './c-user/c-user.component';
import { HomeComponent } from './c-user/components/home/home.component';
import { PagenotfoundComponent } from './c-user/components/pagenotfound/pagenotfound.component';
import { ProfileComponent } from './c-user/components/profile/profile.component';

import { AdnhomeComponent } from './c-admin/components/adnhome/adnhome.component';
import { UsersComponent } from './c-admin/components/users/users.component';

import { PostComponent } from './components/post/post.component';
import { ChatappComponent } from './components/chatapp/chatapp.component';
import { OthersprofileComponent } from './components/othersprofile/othersprofile.component'
import { ViewpostComponent } from './c-user/components/viewpost/viewpost.component';


const routes: Routes = [
  {
    path: "admincp", component: CAdminComponent,
    children: [
      { path: "", redirectTo: "adminhome", pathMatch: "full" },
      { path: 'adminhome', component: AdnhomeComponent },
      { path: 'userlist', component: UsersComponent },
      { path: '**', component: PagenotfoundComponent }

    ]
  },

  {
    path: "", redirectTo: "home", pathMatch: "full"
  },

  {
    path: 'home', component: CUserComponent,
    children: [
      { path: "", redirectTo: "userhome/1", pathMatch: "full" },
      { path: "userhome", redirectTo: "userhome/1", pathMatch: "full"  },
      { path: 'userhome/:page', component: HomeComponent , canActivate:[PagenumberGuard]},
      { path: 'post/:id', component: ViewpostComponent},
      { path: 'addpost', component: PostComponent },
      { path: 'profile', component: ProfileComponent },
      { path: 'chat', component: ChatappComponent },
      { path: 'user/:id', component: OthersprofileComponent },
      { path: '**', component: PagenotfoundComponent }

    ]
  },
  {
    path: '**', component: CUserComponent,
    children: [
      { path: '**', component: PagenotfoundComponent }

    ]
  }

];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})

export class AppRoutingModule { }
